.container {
  height: 100%;
  width: 100%;
  overflow: auto;
}
.container .sp3-container {
  margin: 0 10% 0 10%;
}
.container .sp3-container .lead {
  font-size: 25px;
  font-weight: normal;
  color: black;
  margin-top: 30px;
}
.container .sp3-container .sp3-con-form {
  float: left;
  width: 70%;
  margin-right: 1%;
  height: 100%;
}
.container .sp3-container .sp3-con-form .sp3-form-input {
  width: 90%;
  margin-right: 1%;
}
.container .sp3-container .sp3-con-form .sp3-radio span {
  color: black;
}
.container .sp3-container .sp3-con-form Button {
  background-color: #337ab7;
  border-color: #2e6da4;
}
.container .sp3-container .sp3-con-form .sp3-container-griffiths {
  width: 100%;
  height: 400px;
  margin-bottom: 1%;
}
.container .sp3-container .sp3-container-rank {
  float: right;
  width: 27%;
  height: 100%;
  margin: 1% 0 1% 1%;
  align-items: center;
}
.container .sp3-container .ant-tag {
  margin-bottom: 8px;
}
.container footer {
  margin: 0 10% 0 10%;
}
.container footer h5 {
  font-size: 5px;
}
